.question {
    position: relative;
    color: #2E313F;
    width: 100%;
    max-width: 800px;
    padding: 50px;
    margin: 0 auto;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    box-sizing: border-box;
    background: #FFFFFF;
    border: 3px solid #2E313F;
    border-radius: 20px;
}

.question.game{
    width: 600px;
    /*min-height: 650px;*/
    padding: 0;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.df {
   display: flex;
    justify-content: center;
}
.ornament{
    width: calc(100% + 100px);
    margin-left: -50px;
}
.ornament img{
    width: 100%;
}

.header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: #fff;
}
.container{
    max-width: 1250px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.logo, .logoHmarochos{
    /*height: 35px;*/
    display: flex;
    padding: 0 30px;
    justify-content: center;
    color: #000000;
    text-decoration: none;
    text-align: left;
    font-size: 14px;
    border: none;
    font-family: 'Open Sans', serif;;
    height: 100%;
    background-color: white;
}
.logo:hover{
    text-decoration: underline;
    cursor: pointer;
}

.logo img{
    /*height: 35px;*/
    margin-left: 17px;
}

.logoHmarochos img{
    width: 160px;
}

.logo{
    align-items: center;
    line-height: 1;
    text-transform: uppercase;
}

.logoHmarochos{
    /*padding: 20px 30px;*/
    /*height: 50px;*/
    display: flex;
    align-items: center;
}




.questionContainer {
    position: relative;
    min-height: 100vh;
    /*min-height: -webkit-fill-available;*/
    padding-top: 100px;
    box-sizing: border-box;
    display: flex;
    align-items: flex-start;
    background-size: cover;
    color: #000000;
    transition: opacity 1s ease-in-out, backdrop-filter 1s ease-in-out;
    background-attachment: fixed;
}

.blur{
    background-color: rgb(255,243,216,0.41);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 1s ease-in-out;
}

.social{
    display: flex;
    justify-content: center;
}

.question .answerContainer {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    flex-wrap: wrap;
    transition: opacity 1s ease-in-out;
    align-items: stretch;
    align-content: stretch;
    /*align-content: stretch*/
    /*margin-left: 90px;*/
}


.question .answer {
    display: flex;
    margin: 0px auto 20px;
    transition: opacity 1s ease-in-out;
    width: 50%;

}


.question .answer button, .button {
    background: #32BB78;
    border-radius: 20px;
    color: #ffffff;
    width: 100%;
    height: 70px;
    max-width: 227px;
    padding: 16px;
    text-align: center;
    display: flex;
    align-items: center;
    border: none;
    cursor: pointer;
    transition: 0.5s;
    font-family: "Open Sans", sans-serif;
    margin: 0 auto;
    font-weight: 700;
    justify-content: center;
    box-sizing: border-box;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 28px;
    line-height: 38px;
    /* border: 1px solid transparent */
}

.question .answer button.rightAnswer, .button.rightAnswer {
    background-color: rgb(241,196,105);
    color: rgb(93, 39, 0);
    text-transform: uppercase;
}

.question .answer button.wrongAnswer, .button.wrongAnswer {
    background-color: rgba(241, 105, 105, 0.66);
    color: rgb(93, 39, 0);
}

.question .answer button.withImage .image{
    height: 100px;
    margin-right: 15px;
}
.question .answer button.withImage .icon{
    display: none;
}


.question .answer button:hover,  .button:hover {

    transition: 0.5s ease-in-out;
    box-shadow: 0px 14px 34px rgba(104, 142, 165, 0.3);
    /* border: 1px solid #1de5ac */
}
.question .answer button.rightAnswer:hover,  .button:hover.rightAnswer {

    transition: 0.5s ease-in-out;
    box-shadow: 0px 14px 22px #bda5756e;
    /* border: 1px solid #1de5ac */
}

.question .answer button:disabled:hover, .button:disabled:hover{
    box-shadow: none;
}

.question .answer button.startButton, .button.startButton{
    min-height: auto;
    font-size: 22px;
}


