    border-radius: 10px;
}

.accent {
    color: #f5db00;
}

.rate{
    max-width: 300px;
    margin: 20px auto;
}
.rateText{
    margin-top: 20px;
    font-weight: bold;
}
.stars{
    /*color: #1de5ac;*/
    margin-bottom: 20px;
}

.question .characterWindow {
    width: 47%;

}
.question .characterWindow button{
    flex-direction: column;
    text-transform: none;
    height: 100%;
}

.question .characterWindow button .image{
    margin-bottom: 15px;
    height: 150px !important;
    margin-left: 0;

}
.question .characterWindow button b{
    font-weight: bold;
    color: #1de5ac;
}
.question .characterWindow button p{
    margin: 7px 0;
}

.resultContainer{
   max-width: 300px;
    margin-left: 15px;
}
.result{
    justify-content: center;
}
.result .image{
    max-height: 200px;
}
.resultStarts{
   margin: 15px 0;
}

.resultText{
    margin-bottom: 53px;
    margin-top: 30px;
    display: block;
    text-align: center;
}
.resultText .author{
    display: block;
    font-size: 14px;
    color: rgba(0, 58, 93, 0.67);
}
.resultText b{
    color: #1de5ac;
    text-transform: uppercase;
}


.modal{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 400px;
    background-color: #ffffff;
    color: #000000;
    border: 2px solid #f5db00;
    border-radius: 25px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Open Sans', serif;
    box-sizing: border-box;
}

.modal img{
    height: 50px;
    margin-bottom: 30px;
}

.modal .button{
    text-align: center;
    justify-content: center;
}

.loader {
    height: 100vh;
    width: 100vw;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255);
    backdrop-filter: blur(5px);
    opacity: 1;
    z-index: 20;
    top: 0;
    left: 0;
}
.loader img{
    max-height: 80px;
}

.nowrap{
    flex-wrap: nowrap !important;
}

.car__body {
    animation: shake 0.2s ease-in-out infinite alternate;
}

.carLine {
    transform-origin: center right;
    stroke-dasharray: 22;
    animation: line 0.8s ease-in-out infinite;
    animation-fill-mode: both;
}

.carLineTop {
    animation-delay: 0s;
}

.carLineMiddle {
    animation-delay: 0.2s;
}

.carLineBottom {
    animation-delay: 0.4s;
}


.questionText p{
    margin: 10px 0 0 0;
}

.questionText p a{
    color: #32bb78;
}

@keyframes shake {
    0% {
        transform: translateY(-1%);
    }

    100% {
        transform: translateY(3%);
    }
}

@keyframes line {
    0% {
        stroke-dashoffset: 22;
    }

    25% {
        stroke-dashoffset: 22;
    }

    50% {
        stroke-dashoffset: 0;
    }

    51% {
        stroke-dashoffset: 0;
    }

    80% {
        stroke-dashoffset: -22;
    }

    100% {
        stroke-dashoffset: -22;
    }
}

.finish{
    background-color: #18a980 !important ;
}


.resultText img{
    max-height: 150px;
    margin: 30px;
}

.question .car{
    margin-bottom: 30px;
}

.question .logo {
    margin-left: auto;
    padding-right: 0;
}

