    transition: 0.5s ease-in-out;
}

.nextButton:hover {
    color: rgb(241,196,105);
}

.nextButton:hover svg {
    fill: rgb(241,196,105);
}
.gameInside{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /*min-height: 650px;*/
}

.gameHeader{
    background: #32BB78;
    height: 75px;
    padding: 0 36px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 3px solid #2F313F;
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    /* identical to box height */
    color: #000000;
    text-align: left;
    text-transform: uppercase;
}
.gameHeader b, .gameHeader span{
    display: block;
}
.gameHeader b{
    font-weight: 700;
    font-size: 17px;
    line-height: 23px;
}

.gameHeader .amount{
    text-align: right;
}

.gameFooter{
    background: #FFFFFF;
    border-top: 3px solid #2F313F;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 110px;
}


.gameButton{
    width: 207px;
    height: 103.62px;
    border: 1px #000000;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #fff;
    cursor: pointer;
}
.gameButton:focus{
    outline: none;
    border: none;
}




.gameArea{
    height:100%;
    width: 100%;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    background-image: url("./img/game_bg.png");
    background-position: center center;
    background-size: 105% 105%;
    scroll-behavior: auto;
}

.gameArea .bg{
    /*height: 166.6vw;*/
    width: 100%;
    margin: 0 auto;
    background: url("./img/grass_long_screen.png") repeat-y center;
    background-size: 100% auto;
    position: absolute;
    top: 0;
    left: 0;
}
.gameArea .bg.run{
    animation: slide 3s linear infinite;
}
.scooter{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 108px;
    width: 33%;
    background-image: url("./img/scooter.png");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: contain;
    transition: .05s linear;
    z-index: 23;
}
.obstacle{
    width: 32%;
    background-color: red;
    height: 160px;
    margin: 0 auto;
    position: absolute;
    top: 0;
    left: 0;
}
@keyframes slide {
    0% {
        transform: translateY(-50%);
    }
    100% {
        transform: translateY(0); /* The image width */
    }
}

